<template>
  <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
    <v-btn color="green darken-3" class="ma-2" dark fixed bottom right fab slot="activator">
      <v-icon>add</v-icon>
    </v-btn>
    <v-card>
      <v-toolbar>
        <v-toolbar-title class="green--text text--darken-3 headline">
          <small>Нова задача</small>
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="cancel" :disabled="isLoading || hasError">
          <v-icon>close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-layout row justify-center>
        <v-flex xs12 lg7 mb-3>
          <v-card-text>
            <v-form v-model="valid" lazy-validation ref="newTaskForm">
              <v-layout row wrap>
                <v-flex xs12 sm6>
                  <v-switch label="Задача на час" class="mt-2" color="green darken-3" v-model="needDate"></v-switch>
                </v-flex>
                <v-flex xs12 sm6>
                  <v-switch label="Додатковий опис" class="mt-2" color="green darken-3" v-model="needDesc"></v-switch>
                </v-flex>
              </v-layout>
              <v-text-field prepend-icon="title" color="green darken-3" name="title" label="Заголовок" type="text" v-model.trim="title" :rules="[rules.required]" required></v-text-field>
              <transition name="slide-y-transition">
                <div v-if="needDesc">
                  <v-text-field prepend-icon="description" color="green darken-3" name="description" label="Додатковий опис" type="text" multi-line v-model.trim="description" :counter="100" :rules="[rules.required, rules.minLength]"></v-text-field>
                </div>
              </transition>
              <c-date-picker :onDate="onDate" :needDate="needDate"></c-date-picker>
              <c-time-picker :onTime="onTime" :date="selectedDate"></c-time-picker>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="grey--text text--darken-2" flat :disabled="isLoading || hasError" @click="cancel">Скасувати</v-btn>
            <v-btn color="green darken-3 white--text" :disabled="!valid || isLoading || hasError" :loading="isLoading" @click="createTask">Створити</v-btn>
          </v-card-actions>
        </v-flex>
      </v-layout>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "NewTask",
  data() {
    return {
      valid: true,
      dialog: false,
      title: "",
      description: "",
      needDesc: false,
      needDate: false,
      selectedDate: null,
      selectedTime: null,
      rules: {
        required: value =>
          (value && value.trim().length != 0) || "Обов'язкове поле.",
        minLength: value =>
          (value && value.length <= 100) || "Макс. к-сть 100 символів."
      }
    };
  },
  components: {
    "c-date-picker": () => import(`@/components/DatePicker.vue`),
    "c-time-picker": () => import(`@/components/TimePicker.vue`)
  },
  computed: {
    deadline() {
      if (this.needDate) {
        return new Date(`${this.selectedDate} ${this.selectedTime}`);
      } else {
        return null;
      }
    },
    ...mapGetters(["isLoading", "hasError"])
  },
  methods: {
    onDate(date) {
      this.selectedDate = date;
    },
    onTime(time) {
      this.selectedTime = time;
    },
    createTask() {
      if (this.$refs.newTaskForm.validate()) {
        this.$store
          .dispatch("createNewTask", {
            title: this.title,
            description: this.description,
            deadline: this.deadline
          })
          .then(() => {
            this.cancel();
          });
      }
    },
    cancel() {
      this.dialog = false;
      this.$refs.newTaskForm.reset();
    }
  }
};
</script>



